.List{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 10px;
    margin-left:20%;
    margin-right:20%;
    margin-top: 20px;
    height: 200px;
}
.List div{
    background-color: rgb(26, 23, 23);
    align-items: center;
    text-align: center;
}
.List img{
    object-fit: contain;
    height: 90%;
    width: 60%;
}
.List h3{
    margin: 0px;
    color: yellowgreen;
}
.header h1{
    color: rgb(254, 254, 253);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
input{
    padding: 10px;
    width: 20%;
    border: none;
    border-radius: 10px;
    margin-right: 2px;
    background-color: black;
    border: 1px solid yellow;
    color: rgb(202, 245, 101);
    font-size: medium;
}
button{
    padding: 10px;
    width: 10%;
    border: none;
    border-radius: 10px;
    background-color: rgb(224, 150, 14);
    color: white;
    cursor: pointer;
    margin-left: 2px;
}